*{
    margin:0px;
    padding:0px;
    box-sizing:border-box;
}


.house_container{
    width:80vw;
    height:100%;
    background:#ccc;
    float:left;
    border:1px solid #ccc;
    padding:10px;


}

.house_main{
    width:100%;
    height:80%;
    background-color:#f1f1f1;

    
}

.home_cards{
    display:grid;  
    grid-template-columns:1fr 1fr;
    column-gap:20px;
    row-gap:100px;
    padding:100px;
}



.home_cards .card{
     width:100%;
    height:300px;
    background-color:#fff;
    border:1px solid #000;
    padding:10px;
    
    
}

.home_cards .card .image{
    display:flex;
    justify-content:center;
}

.home_cards .card img{
    width:100%;
    height:200px;

    


}
