*{
    margin:0px;
    padding:0px;
    box-sizing:border-box;
}

.container{
    background-color:#ddd;
    width:100vw;
    height:100%;
    border:1px solid red;
    padding:50px 100px;

}
.main{
    width:100%;
    height:100%;
    border:1px solid #ccc;
    box-shadow:1px 1px 2px #ccc;
    background-color:#f1f1f1;
    padding:30px;
    margin-bottom:40px;


}

.top_block{
    width:100%;
    height:400px;
    background-color:#fff;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-wrap:wrap;
    flex-direction:column;
    gap:20px;
    border:1px solid #ccc;
    margin:20px 0px;


}

.search{
    display:flex;
    flex-direction:column;
    flex-wrap:wrap;
    gap:10px;
}
.top_block .search input[type="text"]{
    width:20vw;
    height:60px;
    border:1px solid #444;
    padding:20px;


}
.search .select_option{
    padding:10px;
    width:150px;
    height:60px;
    border:1px solid #444;

}

.search .select_option li{
    padding:30px;
    color:red;
    width:150px;
    height:100px;
    border:1px solid #444;

}
.search input[type="submit"]{
    padding:10px;
    height:40px;
    width:100px;
    border:1px solid #444;
}

/* mid level */

.mid_block{
    width:100%;
    height:100vh;
    background-color:#ddd;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    flex-wrap:wrap;
    flex-direction:column;
    margin-top:200px;
    padding:10px;
    align-content:flex-start;

}

.mid_block .featured{
    width:40%;
    height:100vh;
    background:#ddd;


}

.mid_block .featured .featured_post .post1{
    width:400px;
    height:100px;
    background-color:#fff;
    border:1px solid #ccc;
    margin:15px;



}

.mid_block .featured h2{
    text-align:left;
    margin-top:20px;
    

}
.center_block{
    width:100%;
    height:400px;
    margin-top:80px;
    border:1px solid #ccc;
    
}


.center_block .cards{
    width:100%;
    height:400px;
    border:1px solid #ccc;
    background-color:#fff;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    padding:20px;
    

}

.center_block .cards .card{
    width:100%;
    height:400px;
    display:flex;
    gap:10px;
    margin:10px;

}

.center_block .cards .card .card1{
    width:100%;
    height:100px;
    background-color:#ddd;
    border:1px solid #ccc;


}
