*{
    margin:0px;
    padding:0px;
    box-sizing:border-box;
}


.header{
    background: #fff;
    width: 100vw;
    height: 15vh;
    display: flex;
    gap: 200px;
    align-items: center;
    padding: 30px;
    border:1px solid #BB6464;
}

.login ul{
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction: row;
    
}

.login ul li{
    list-style:none;
    padding:10px;
    
}

.login ul li a{
text-decoration:none;
padding:10px;
color:#444;
font-size:17px;
font-weight:500;
}
.login ul li a:hover{
    color: #BB6464;
}





.img img{
    width:100px;
    height:100px;
    padding:20px;
    border-radius:50%;

}

.main_header ul li{
    list-style:none;
 
    
}

.main_header ul li a{
    text-decoration:none;
    padding:10px 30px;
    color:#444;
    font-size:17px;
    font-weight:500;

}

.main_header ul li a:hover{
    background:#BB6464;
    color:#fff;
    
}
